import { createContext } from 'preact';

/**
 * Defines user specific environment
 */
type User = {
  locale: string;
  direction: 'rtl' | 'ltr';
  forcedColors: 'none' | 'active';
};

type ColorScheme = 'light' | 'dark';
type Scale = 'lg' | 'md' | 'sm';

/**
 * Defines theme environment on root element
 */
type Theme = {
  name: string;
};

/**
 * Defines translation bundle type.
 */
export type TranslationBundle = Record<string, (...args: any[]) => string>;

/**
 * Defines a type for translation property which is an object where
 * - key - string - bundle id
 * - value - object - strings to functions of the type (options?: object)=>string
 *
 * Translation bundle example:
 * const bundle = {
 *  @oracle/oracle-preact-bundle: {
 *      welcome: () => 'bienvenido',
 *      success: () => 'Ã©xito'
 *  }
 * }
 */
type Translations = { [bundleId: string]: TranslationBundle };

/**
 * Environment specified at the root level
 */
export type RootEnvironment = {
  // supports user preferences
  user?: Partial<User>;
  // supports theme settings
  theme?: Theme;
  colorScheme?: ColorScheme;
  scale?: Scale;
  // supports translation bundles
  translations?: Translations;
};

/**
 * Environment specified at the component level
 */
export type Environment = {
  colorScheme?: ColorScheme;
  scale?: Scale;
  translations?: Translations;
};

export type CompleteEnvironmentType = {
  user: User;
  theme: Theme;
  scale: Scale;
  colorScheme: ColorScheme;
  translations?: Translations;
};

/**
 * Default environment created for the application
 */
export const DefaultEnvironment: CompleteEnvironmentType = {
  user: {
    locale: document.documentElement.getAttribute('lang') || 'en',
    direction: document.documentElement.getAttribute('dir') === 'rtl' ? 'rtl' : 'ltr',
    forcedColors: window.matchMedia?.('(forced-colors: active)')?.matches ? 'active' : 'none'
  },
  theme: {
    name: 'redwood'
  },
  colorScheme: 'light',
  scale: 'lg'
};

export const EnvironmentContext = createContext<CompleteEnvironmentType>(DefaultEnvironment);
